.home {
    position: relative;
    min-height: 100%;
    overflow: hidden;

    &__header {
        padding: 15px 18px;
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        align-items: center;

        &-logo {
            width: 22%;
        }
    }

    &:after {
        content: "";
        background: #fe5722;
        position: absolute;
        z-index: -1;
        width: 140%;
        height: 20rem;
        border-radius: 50%;
        left: -18%;
        top: 0;
    }

    &__content {
        .padded {
            padding: 1.2rem;
        }
    }


    h1 {
        font-size: 2rem;
    }

    &__restaurants {
        padding: 0 1.2rem; 
        display: flex;
        flex-flow: row nowrap;
        overflow-x: scroll;

        .restaurant-card__outer:last-child {
            padding-right: 1.2rem;
        }
    }

    &__more-ordered {
        margin-top: 2rem;
    }
}
